.range-slider {
  width: 266px;
  height: 48px;
}

.content {
  width: 266px;
}

.slider {
  display: block;
  position: relative;
  height: 12px;
  width: 100%;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.slider .slider-touch-left, .slider .slider-touch-right {
  display: block;
  position: absolute;
  height: 12px;
  width: 12px;
  padding: 0px;
  z-index: 2;
}
.slider .slider-touch-left span, .slider .slider-touch-right span {
  display: block;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
  border: 2px solid #FFFFFF;
  border-radius: 10px;
  margin-top: 12px;
}
.slider .slider-line {
  margin-right: 90px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  position: absolute;
  width: 100%;
  left: 0px;
  top: 16px;
  height: 6px;
  border-radius: 3px;
  background: #FFFFFF;
  z-index: 0;
  overflow: hidden;
}
.slider .slider-line span {
  display: block;
  height: 100%;
  width: 0%;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
}

.range-text {
  font-family: "MontserratBold";
  font-size: 12px;
  line-height: 15px;
  text-align: right;
  text-transform: uppercase;
  text-align: left;
  color: #1F2041;
}

.range-result {
  margin-top: -15px;
  font-family: "MontserratRegular";
  color: rgba(31, 32, 65, 0.5);
  font-size: 12px;
  line-height: 15px;
  text-align: right;
  text-transform: uppercase;
}