.checkbox-list {
  display: grid;
  gap: 12px;
}
.checkbox-list_rich {
  gap: 10px;
}
.checkbox-list__list {
  margin: 0px;
}
.checkbox-list__item {
  list-style: none;
}
.checkbox-list__label {
  width: 236px;
  padding-left: 30px;
  position: relative;
  display: block;
  cursor: pointer;
}
.checkbox-list__input {
  position: absolute;
  opacity: 0;
}
.checkbox-list__input:checked + .checkbox-list__text::before {
  border-color: #bc9cff;
}
.checkbox-list__input:checked + .checkbox-list__text::after {
  content: "";
  display: block;
  border-bottom: 2px solid #bc9cff;
  border-right: 2px solid #bc9cff;
  transform: rotate(45deg);
  width: 5px;
  height: 6px;
  position: absolute;
  top: 5px;
  left: 6px;
}
.checkbox-list__text {
  font-family: "MontserratRegular";
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.5);
}
.checkbox-list__text::before {
  content: "";
  border: 1px solid rgba(31, 32, 65, 0.25);
  width: 18px;
  height: 18px;
  position: absolute;
  display: block;
  border-radius: 4px;
  left: 0;
}
.checkbox-list_rich .checkbox-list__text {
  font-weight: bold;
  color: rgba(31, 32, 65, 0.75);
}
.checkbox-list__sub-text {
  margin-top: 4px;
  color: rgba(31, 32, 65, 0.5);
  font-family: "MontserratRegular";
  font-size: 12px;
  line-height: 14px;
}